import styled, { css } from "styled-components";
import { Property } from "csstype";

type PropertyLocal =
  | "color"
  | "background-color"
  | "opacity"
  | "visibility"
  | "border-color"
  | "border-radius"
  | "box-shadow"
  | "text-shadow"
  | "transform"
  | "outline"
  | "top"
  | "right"
  | "bottom"
  | "left"
  | "height"
  | "width"
  | "margin";
type Animation = Property.AnimationTimingFunction;

export const getTransition = (
  duration: number = 150,
  property: PropertyLocal[] | PropertyLocal = ["background-color", "color"],
  animation: Animation = "linear"
) => css`
  transition-property: ${Array.isArray(property)
    ? property.join(", ")
    : property};
  transition-duration: ${duration}ms;
  transition-timing-function: ${animation};
`;

export const ScreenReader = styled.label`
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  width: 1px;
  height: 1px;
  overflow: hidden;
`;

export const visible = css`
  opacity: 1;
  visibility: visible;
`;

export const hidden = css`
  opacity: 0;
  visibility: hidden;
`;

export const ImagePlaceholder = css`
  font-size: 1.125rem;
  text-anchor: middle;
  user-select: none;
`;

export const fullHide = css`
  opacity: 0;
  visibility: hidden;
  display: none;
`;

export const scrollBar = css`
  ::-webkit-scrollbar {
    width: 0.5rem;
    height: 1rem;
  }

  ::-webkit-scrollbar-thumb {
    background: ${({ theme }) => theme.colors.grey26};
    border-radius: 16px;
    box-shadow: inset 2px 2px 2px
        hsla(${({ theme }) => theme.colors.fontValue}, 0.25),
      inset -2px -2px 2px hsla(${({ theme }) => theme.colors.revertValue}, 0.25);
  }

  ::-webkit-scrollbar-track {
    background: linear-gradient(
      90deg,
      ${({ theme }) => theme.colors.grey26},
      ${({ theme }) => theme.colors.grey26} 1px,
      ${({ theme }) => theme.colors.grey7} 0,
      ${({ theme }) => theme.colors.grey7}
    );
  }
`;
