import { gql } from "@apollo/client";
import { Callorder, Cardservice, Cite, Feature } from "./typeDefs";

export type IsUserLoggedIn = {
  isLoggedIn: boolean;
};
const IS_LOGGED_IN = gql`
  query IsUserLoggedIn {
    isLoggedIn @client
  }
`;
export type GetServiceCardType = {
  GetServiceCard: GetServiceCard;
  GetServiceCardVars: GetServiceCardVars;
};
type GetServiceCard = {
  getCards: Cardservice[];
};
type GetServiceCardVars = {
  idx: string;
};
const GET_SERVICE_CARD = gql`
  query GetServiceCard($idx: String!) {
    getCards(idx: $idx) {
      id
      position
      sku
      name
      srcImg
      describe
      orderCount
      available
      price
      slug
      tags
    }
  }
`;

export type GetServiceCards = {
  getCards: Cardservice[];
};

const GET_SERVICE_CARDS = gql`
  query GetServiceCards {
    getCards {
      id
      position
      sku
      name
      srcImg
      describe
      orderCount
      createdAt
      updatedAt
    }
  }
`;

export type GetFeatures = {
  getFeatures: Feature[];
};
const GET_FEATURES = gql`
  query GetFeatures {
    getFeatures {
      id
      icon
      description
      title
    }
  }
`;

export type GetRandomCite = {
  getRandomCite: Cite;
};
const GET_RANDOM_CITE = gql`
  query GetRandomCite {
    getRandomCite {
      id
      content
    }
  }
`;

export type GetCallorders = {
  getCallorders: Callorder[];
};
const GET_CALLORDERS = gql`
  query GetCallorders {
    getCallorders {
      id
      phone
      describe
      createdAt
      email
      name
    }
  }
`;

export {
  IS_LOGGED_IN,
  GET_SERVICE_CARDS,
  GET_SERVICE_CARD,
  GET_RANDOM_CITE,
  GET_FEATURES,
  GET_CALLORDERS
};
