import React, { MutableRefObject, useCallback, useRef } from "react";

type PhoneSampleProps = {
  value: string;
  change: (e: React.ChangeEvent<HTMLInputElement>) => void;
  message?: string;
  blur?: (e: React.FocusEvent<HTMLInputElement>) => void;
};
export const PhoneSample = ({
  value,
  message,
  change,
  blur
}: PhoneSampleProps) => {
  const phone = useRef() as MutableRefObject<HTMLInputElement>;
  const getPhone = useCallback(() => {
    const valueArr = [...value];
    if (!value.length) return "(___) ___-____";
    const mask = ["("];
    mask[1] = valueArr[0] || "_";
    mask[2] = valueArr[1] || "_";
    mask[3] = valueArr[2] || "_";
    mask[4] = ")";
    mask[5] = " ";
    mask[6] = valueArr[3] || "_";
    mask[7] = valueArr[4] || "_";
    mask[8] = valueArr[5] || "_";
    mask[9] = "-";
    mask[10] = valueArr[6] || "_";
    mask[11] = valueArr[7] || "_";
    mask[12] = valueArr[8] || "_";
    mask[13] = valueArr[9] || "_";
    return mask.join("");
  }, [value]);
  const onFocus = (e: React.FocusEvent<HTMLInputElement>) => {
    phone.current.value = e.target.value.replace(/[()_\-\s]+/g, "").trim();
  };
  const onBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    if (blur) blur(e);
    phone.current.value = getPhone();
  };

  return (
    <input
      ref={phone}
      name="phone"
      type="text"
      className={`form-control ${
        message && (message === "isValid" ? "is-valid" : "is-invalid")
      }`}
      id="phone"
      placeholder="999-999-99-99"
      value={value}
      onFocus={onFocus}
      onBlur={onBlur}
      onChange={change}
      required
    />
  );
};
