import React, { useState } from "react";

export type EventWithTarget = {
  target: {
    name: string;
    value: string;
  };
};

export const useFormInput = <
  T extends Object,
  E extends React.ChangeEvent<HTMLElement> & EventWithTarget
>(
  initialValue: T
): [
  { value: T; onChange(e: E): void },
  () => void,
  (name: string, value: string) => void
] => {
  const [value, setValue] = useState(initialValue);
  return [
    {
      value,
      onChange: (e) => {
        if (e.target.name === "checkbox") {
          if (e.target.value === "false") {
            setValue({
              ...value,
              [e.target.name]: "true"
            });
          } else {
            setValue({
              ...value,
              [e.target.name]: "false"
            });
          }
        } else if (e.target.name === "login") {
          if (e.target.value.includes(".") && e.target.value.includes("@")) {
            if (e.target.value.length >= 200) return;
            setValue({
              ...value,
              email: e.target.value,
              username: ""
            });
          } else {
            if (e.target.value.length > 32) return;
            setValue({
              ...value,
              username: e.target.value,
              email: ""
            });
          }
        } else if (e.target.name === "email") {
          if (e.target.value.length >= 200) return;
          setValue({
            ...value,
            email: e.target.value
          });
        } else if (e.target.name === "content" || e.target.name === "message") {
          if (e.target.value.length >= 1000) return;
          setValue({
            ...value,
            [e.target.name]: e.target.value
          });
        } else if (
          e.target.name === "file" &&
          e.target instanceof HTMLInputElement
        ) {
          if (!e.target.files) return;
          if (!e.target.multiple) {
            e.target.files.length
              ? setValue({
                  ...value,
                  [e.target.name]: e.target.files[0]
                })
              : setValue({
                  ...value,
                  [e.target.name]: ""
                });
          } else return;
        } else if (e.target.name === "phone") {
          const currentValue = e.target.value;
          const isCheck = checkPhone(e.target.value);
          if (!isCheck) return;
          setValue({
            ...value,
            phone: currentValue
          });
        } else {
          if (e.target.value.length > 32) return;
          setValue({
            ...value,
            [e.target.name]: e.target.value
          });
        }
      }
    },
    () => setValue(initialValue),
    (name, newValue) => {
      setValue({
        ...value,
        [name]: newValue
      });
    }
  ];
};

const checkPhone = (value: string) => {
  if (/\D/.test(value) && !/\([\d_]{3}\)\s[\d_]{3}-[\d_]{4}/.test("value"))
    return false;
  const currentValue = value.replace(/[()_\-\s]+/g, "").trim();
  return !(currentValue.length > 10 || /(\D+)/.test(currentValue));
};
