import * as S from "./Styles";

const stages = [
  {
    stage: 1,
    name: "Оформление"
  },
  {
    stage: 2,
    name: "Контактные данные"
  },
  {
    stage: 3,
    name: "Описание работы"
  },
  {
    stage: 4,
    name: "Подтверждение"
  }
];

type SubHeaderProps = {
  currStage: number;
  isHidden: boolean;
};

export const SubHeader = ({ currStage, isHidden }: SubHeaderProps) => {
  const getStatus = (stage: number) => {
    if (stage === currStage) return "active";
    else if (stage < currStage) return "on";
    else return "off";
  };
  return (
    <S.SubHeader isHidden={isHidden}>
      <S.NumStageWrapper>
        {stages.map(({ stage }) => (
          <S.NumCircle status={getStatus(stage)} key={stage}>
            <S.Number status={getStatus(stage)} isFour={stage === 4}>
              {stage}
            </S.Number>
          </S.NumCircle>
        ))}
        <S.LineProgress>
          <S.ProgressBar stage={currStage} />
        </S.LineProgress>
      </S.NumStageWrapper>
      <S.DescribeWrapper>
        {stages.map(({ stage, name }) => (
          <S.StageDescribe isActive={stage === currStage} key={name}>
            {name}
          </S.StageDescribe>
        ))}
      </S.DescribeWrapper>
    </S.SubHeader>
  );
};
